/* CARD COMPONENT STYLE */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

.wrapper {
  margin: 10px;
  padding: 7px;
  border: 1px solid #f76707;
  border: 2px solid #ddd;
  border-radius: 7px;
  background-color: #fff4e6;
  background: #ddd;
  background: #f5f5f5;
}

.wrapper>div {
  max-width: 100%;
  border-radius: 5px;
  color: #d9480f;
  background: #f5f5f5;
  padding-bottom: 5px;
}

.season-container {
  display: flex;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
}

.wrapper .img {
  padding: 0;
  margin: 0;
}

.img img {
  width: 100%;
  border-radius: 10px;

}

.btn-wrapper {
  display: flex;
}

.btn {
  border: none !important;
}

.btn,
.btn-outline {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
  margin-bottom: 10px;
  padding: 5px 15px;
  background-color: rgb(25, 118, 210);
  color: white;
  font-style: italic;
  font-size: x-small;
  text-transform: uppercase;
  border-radius: 4px;
}

.btn,
.btn-outline:hover {
  cursor: pointer;
  border-color: #ccc;
}

.btn-outline {
  color: rgb(25, 118, 210);
  background-color: #fff;
  border: 1px solid rgb(25, 118, 210);
}

.wrapper h1 {
  font-weight: 600;
  color: #d9480f;
}



